.aside {
	padding: 15px;
	background-color: #7A956B;
	flex: 0 1 30%;
	color: #eee;
}


.aside__nav {
	display: flex;
	flex-direction: column;
	row-gap: 15px;
}

.aside__link {
	color: #eee;
	font-size: 22px;
	font-weight: 700;
}

.aside__link:last-child {
	margin-top: 20px;
}

.aside__friends {
	display: flex;
	column-gap: 15px;
	flex-wrap: wrap;
	row-gap: 15px;
	justify-content: space-between;
}

.friend {
	display: flex;
	flex-direction: column;
	row-gap: 10px;
	align-items: center;
}

.friend__img {
	width: 50px;
	height: 50px;
}

.friend__name {
	font-size: 20px;
}