.dialogs {
	flex: 0 1 70%;
	padding: 15px;
}

.dialogs__friends {
	display: flex;
	flex-direction: column;
	row-gap: 15px;
	flex: 0 1 30%;
}

.dialogs__friend {
	color: #1d1d1d;
}

.dialogs__friend.active {
	text-decoration: underline;
}

.dialogs__row {
	display: flex;
}

.dialogs__messages {
	display: flex;
	flex-direction: column;
	row-gap: 15px;
	flex: 0 1 70%;
}

.message {
	display: flex;
	align-items: center;
	column-gap: 10px;
}

.message__img {
	width: 60px;
	height: 60px;
}

.add-message {
	display: flex;
	flex-direction: column;
}