.users {
	padding: 15px;
	flex: 0 1 70%;
}

.users .btn {
	margin: 0 auto;
	width: 120px;
	text-align: center;
}

.users__row {
	display: flex;
	flex-direction: column;
	row-gap: 20px;
}

.user {
	display: flex;
	align-items: center;
	column-gap: 30px;
}

.user__logo {
	display: flex;
	flex-direction: column;
	align-items: center;
	row-gap: 10px;
}

.user__follow {
	text-transform: uppercase;
	background-color: #4eba0b;
	color: #eee;
	padding: 3px;
}

.user__avatar {
	width: 80px;
	height: 80px;
	border-radius: 50%;
}

.user__content {
	display: flex;
	flex-direction: column;
	row-gap: 10px;
	padding: 10px;
	border: 1px solid #0ec182;
	border-radius: 10px;
	flex: 0 1 40%;
}

.user__top {
	display: flex;
	align-items: flex-end;
}

.user__name {
	flex: 0 1 100%;
	font-size: 22px;
	font-weight: 700;
}

.user__country,
.user__city {
	font-size: 20px;
}

.user__country {
	margin-right: 15px;
}

.btn:disabled {
	opacity: 0.6;
}

.btn.unfollow {
	background-color: red;
}

.btn.follow {
	background-color: green;
}

.pagination-container {
	display: flex;
	align-items: center;
	column-gap: 10px;
}

.pagination-container span {
	cursor: pointer;
}

.selected-page {
	font-size: 20px;
	font-weight: 700;
}

.loader {
	width: 200px;
	height: 200px;
	z-index: 10;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}