body {
  font-family: 'Roboto';
  font-size: 16px;
  font-weight: 500;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

input,
textarea,
button {
  border: none;
  background: none;
  outline: none;
  padding: 0;
  resize: none;
}

img {
  object-fit: cover;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

.input {
  background-color: #160cd4;
  color: #eee;
  padding: 15px;

}

.input::placeholder {
  color: #eee;

}

.btn {
  text-transform: uppercase;
  color: #eee;
  background-color: #2fdd14;
  padding: 10px 0;
  cursor: pointer;
  width: 80px;
  margin-left: auto;
  margin-top: 10px;
}

.title {
  font-size: 35px;
  font-weight: 700;
  margin: 20px 0;
}

.container {
  max-width: 1030px;
  margin: 0 auto;
  padding: 0 15px;
}

.app__content {
  display: flex;
}