.my-profile {
	padding: 15px;
}

.my-profile__content {
	display: flex;
	align-items: center;
	column-gap: 20px;
}

.my-profile__img {
	width: 150px;
	height: 150px;
	object-fit: cover;
}

.my-profile__name {
	font-size: 25px;
	font-weight: 700;
}

.profile-status {
	display: flex;
	flex-direction: column;
	row-gap: 10px;
}

.profile-status__inp {
	background-color: grey;
	padding: 3px;
}