.profile {
	flex: 0 1 70%;
	padding: 20px;
}

.profile__img {
	width: 100%;
	height: 200px;
}

/* CURRENT USER  */

.currentUser {
	margin: 20px 0;
	display: flex;
	align-items: center;
	column-gap: 20px;

}

.currentUser__avatar {
	width: 150px;
	height: 150px;
}

.currentUser__name {
	margin-bottom: 10px;
	font-size: 30px;
}

.speciality__info {
	display: flex;
	flex-direction: column;
	row-gap: 5px;
}

.speciality__info p a {
	color: yellowgreen;
}

/*POSTS & POST  */

.profile__posts {
	display: flex;
	flex-direction: column;
	row-gap: 10px;
}

.post {
	display: flex;
	align-items: center;
	column-gap: 20px;
}

.post__img {
	width: 60px;
	height: 60px;
}

/* ADD POST */



.add-post {
	margin: 30px 0;
	display: flex;
	flex-direction: column;
}